*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



html
{
    font-size: 62.5%;
}

button, select, img
{
  cursor: pointer;
}

.App
{
    width: 90%;
    margin: 0 auto;
}

h1
{
    text-align: center;
}


/* Modo claro */
body.modo-claro {
    background-color: #ffffff;
    color: #000000;
  }
  
  /* Modo oscuro */
  body.modo-oscuro {
    background-color: #000000;
    color: #ffffff;
  }

  body
  {
    transition: background-color 0.5s ease;
  }


.randomQuote
{
    text-align: center;
    font-size: 1.4rem;
}

header .version
{
    font-size: 1.1rem;
}

header{
    display: flex;
    flex-direction: column;
    margin: 10px auto;
    max-width: 850px;
    padding-bottom: 10px;
    border-bottom: 1px solid #dad8d8;
    justify-content: space-between;
}

header .top
{
    display: flex;
    justify-content: space-between;
}

header .left
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* border-bottom: 1px solid #dad8d8; */
    /* padding-bottom: 15px; */
}


header .right
{
    width: 80px;
    height: 80px;
    display: flex;
}

header .right img
{
    align-self: center;
    width: 85%;
}

header .left h2
{
    font-size: 2.5rem;
}

header .left img
{
    width: 70px;
}

header h1
{
    font-size: 4rem;
    margin-top: 10px;
    border: 4px solid #0c8239;
    padding: 15px 0;
    color: #0c8239;
}

header h1 span
{
    font-size: 1.4rem;
}

.darkModeLogo
{
    -webkit-filter: invert(100%);
            filter: invert(100%);
}
.Card_ganzo__zM-MI
{
    color:red;
}

.Card_boton__YXfzu
{
    color: white;
    font-size: 1.4rem;
    font-weight: bold;
    border: none;
    /* border-radius: 10px; */
    background-color: rgb(209, 22, 22);
    padding: 10px 15px;
    transition: 0.2s all ease-in-out;
}



.Card_boton__YXfzu:hover
{
    outline: 2px solid rgb(209, 22, 22);
    background-color: transparent;
    color: rgb(209, 22, 22);
    border-radius: 10px;
}

/* .doneBTN */
#Card_undoBTN__cVxpR
{
    background-color: rgb(247, 232, 23);
    /*pointer-events: none; QUITA EL HOVER! LOOOL*/
    text-decoration: none;

   
}

li.Card_completed__1foxm .Card_li-upper__2_Xjt button {
    text-decoration: none !important;
  }



main
{
    width: 90%;
    margin: 30px auto 10px;
    /* min-width: 600px; */
    max-width: 700px;
    position: relative;
}

.toggleBTN
{
    position: absolute;
    right: 0;
    top: -5px;
    width: 115px;
    font-size: 2rem;
    height: 40px;
    /* border-radius: 10px; */
    outline: 3px solid #f90817;
    border: none;
    background-color: transparent;
    color: #f90817;
    transition: 0.2s all ease-in-out;
    font-weight: bolder;
    display: flex;
    justify-content: center;
    align-items: center;
    /* display: none; */
}

.toggleBTN:hover
{
    background-color: #f90817   ;
    color: white;
    outline: none;
    border-radius: 10px;
}

main ul
{
    padding: 40px 0;
}



ul
{
    list-style: none;
    /* min-width: 600px; */
}

ul li
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 10px;
    font-size: 25px;
    border: 1px solid rgb(102, 101, 101);
    padding: 10px 20px;
    border-left: 5px solid rgb(81, 81, 236);
}

ul li > div:nth-child(1)
{
    display: flex;
    justify-content: space-between;
    grid-area: b;
}

ul li > div:nth-child(1) span
{
    cursor:pointer;
}

ul li > div:nth-child(2)
{
    display: flex;
    grid-area: a;
    /* gap: 10px; */
}

ul li > div:nth-child(2) p
{
    font-size: 1.3rem;
    font-weight: bold;
}

.taskDisplayingTitle
{
    font-size: 1.4rem;
    font-weight: bold;
}



.completedAndTachado
{
    text-decoration: line-through; 
}

.completed
{
    /* text-decoration: line-through; */
    box-shadow: -1px 8px 16px 1px rgba(66,250,51,0.77);
    -webkit-box-shadow: -1px 8px 16px 1px rgba(66,250,51,0.77);
    -moz-box-shadow: -1px 8px 16px 1px rgba(66,250,51,0.77);
    color: rgb(53, 52, 52);
    /* background-color: rgb(169, 248, 169); */

    opacity: 0.8;
    /* background-color: rgb(19, 18, 18); */
    /* background-color: white; */
}

.incomplete
{
    box-shadow: -1px 8px 16px 1px rgba(252,40,26,0.77);
    -webkit-box-shadow: -1px 8px 16px 1px rgba(252,40,26,0.77);
    -moz-box-shadow: -1px 8px 16px 1px rgba(252,40,26,0.77);
}


.tasksFilterButtonContainer
{
    display: flex;
}

.tasksFilterButtonContainer-upper
{
    display: flex;
    grid-gap:10px;
    gap:10px;
}

.tasksFilterButton
{
    background-color: transparent;
    border: none;
    transition: 0.2s all ease-in-out;
    height: 30px;
    width: 90px;
    outline: 3px solid rgb(206, 101, 16);
    color: rgb(206, 101, 16);
    font-weight: bold;
}

/* .tasksFilterButton:hover
{
    color: white;
    background-color: rgb(206, 101, 16);
    outline: none;
    border-radius: 10px;
} */

main ul li .li-lower
{
    align-items: center;
}

main ul li .li-lower .taskDateTitle
{
    padding-right: 2px;
}

li .li-upper button
{
    max-height: 36px    ;
}


.cardButtonsContainer
{
    display: flex;
    grid-gap: 10px;
    gap: 10px;
}

.cardButtonsContainer button:nth-child(1)
{
    background-color: #1d781d;
}

.cardButtonsContainer button:nth-child(1):hover
{
    background-color: transparent;
    color: #1d781d;
    outline: 1px solid #1d781d;
}


.cardButtonsContainer button:nth-child(1)#undoBTN
{
    background-color: #cf8120;
}

.cardButtonsContainer button:nth-child(1)#undoBTN:hover
{
    background-color: transparent;
    color: #cf8120;
    outline: 1px solid #cf8120;
}


.filter-selected
{
    border-radius: 10px;
    background-color: #cf8120;
    color: white;
}













@media (max-width: 750px)
{
    main ul li
    {
        height: 140px;
        /* display: flex;
        flex-direction: column;
        padding: 20px; */

        display: grid;
        grid-template-columns: repeat(3,1fr);
        /* grid-template-rows: repeat(4,1fr);
        grid-template-areas:
            ". . a"
            "b b b"
            "b b b"
            ". . ."; */
        /* gap: 2px; */
        padding: 5px 10px 0 10px;
        

        height: unset;
        grid-template-rows: 1fr 2fr 2fr;
        grid-template-areas:
            ". . a"
            "b b b"
            "b b b";
        padding: 15px;
        }

    main ul li .li-lower
    {
        justify-self: flex-end;
    }

    main ul li .li-upper
    {
        flex-direction: column;
        grid-gap:25px;
        gap:25px;
    }

    main ul li .li-upper .cardButtonsContainer
    {
        flex-direction: column;
    }


}


@media (max-width: 640px)
{
    .li-upper > div span
    {
        font-size: 2.2rem;
    }
}



@media (max-width: 520px)
{
    .tasksFilterButtonContainer
    {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        grid-gap: 15px;
        gap: 15px;
    }


    .tasksFilterButtonContainer-lower 
    {
        display: flex;
        justify-content: center;

    }

    .tasksFilterButtonContainer-lower .toggleBTN
    {
        position: relative;
        display: flex;
        justify-content: center;
        width: 290px;
        /* display: none; */
    }

    main ul li
    {
        grid-template-columns: repeat(2,1fr);
        grid-template-rows: 1fr 2fr 2fr;
        grid-template-areas:
            ". a"
            "b b"
            "b b"
            ". .";
    }

    main ul li .li-lower
    {
        justify-self: flex-end;
    }

    
}

@media (max-width: 490px)
{
    .App .addtask form
    {
        border: 0px solid gray;
    }
}

.footer
{
    margin: 20px;
    flex-direction: column;
    grid-gap:10px;
    gap:10px;
    display: flex;
    justify-content: center;
    font-size: 1.5rem;
    align-items: center;
}

.footer p
{
    max-width: 500px;
    text-align: center;
}
.addtask
{
    width: 70%;
    margin: 15px auto 15px;
    display: flex;
    justify-content: center;
    /* min-width: 775px; */
}

.addtask form
{
    display: flex;
    align-items: center;
    border: 1px solid gray;
    padding: 15px 54px;
    margin: 0 auto;
    grid-gap: 30px;
    gap: 30px;
}

.addtask form label 
{
    font-size: 3rem;
    margin-right: 10px;
}

.addtask form div
{
    display: flex;
    grid-gap: 12px;
    gap: 12px;
}


.addtask form input
{
    height: 40px;
    width: 300px;
    padding-left: 10px;
    font-size: 1.8rem;
}

.addtask form select    
{
    height: 40px;
    padding: 10px;
    font-size: 1.5rem;
    font-weight: bolder;
    text-align: center;
    background-color: transparent;
    outline: 3px solid #9fc205;
    border: none;
    color: #9fc205  ;
    transition: 0.2s all ease-in-out;
}

.addtask form select:hover
{
    color: white;
    background-color: #9fc205;
    outline: none;
    border-radius: 10px;
}

.addtask form button
{
    width: 100px;
    height: 40px;
    font-size: 1.5rem;
    font-weight: bolder;
}

.addtask form .reset
{
    background-color: transparent;
    outline: 3px solid #b52020;
    border: none;
    color: #b52020;
    transition: 0.2s all ease-in-out;
    
}

.addtask form .reset:hover
{
    color: white;
    background-color: #b52020;
    outline: none;
    border-radius: 10px;
}

.addtask form .addtaskBTN
{
    background-color: transparent;
    outline: 3px solid rgb(32, 175, 32);
    border: none;
    color: rgb(32, 175, 32);
    transition: 0.2s all ease-in-out;
}

.addtask form .addtaskBTN:hover
{
    color: white;
    background-color: rgb(32, 175, 32);
    outline: none;
    border-radius: 10px;
}



@media (max-width: 870px)
{
    .addtask form
    {
        flex-direction: column; 
        grid-gap:20px; 
        gap:20px;
    }

    .addtask form div
    {
        width: 100%;
    }

    .addtask form div select,.addtask form div button
    {
        width: 50%;

    }

    .addtask form input
    {
        width: 343px;
    }
}
